<template>
  <div>
    <book-header :filterBy="filterBy" />
    <book-list v-if="books.length !== 0" :booksToShow="booksToShow" />
    <loader v-else></loader>
  </div>
</template>

<script>
import BookList from "@/components/BookList";
import BookHeader from "@/components/BookHeader";
import Loader from "@/components/Loader";
import { bookService } from "@/service/bookService";

export default {
  data() {
    return {
      books: [],
      filterBy: {
        title: "",
        minPageRange: "",
        maxPageRange: ""
      }
    };
  },
  async mounted() {
    this.books = await bookService.getBooks();
  },
  components: {
    BookList,
    BookHeader,
    Loader
  },
  computed: {
    booksToShow() {
      let booksToShow = [...this.books];

      const { title, minPageRange, maxPageRange } = this.filterBy;
      console.log("type of ", typeof minPageRange);
      if (title) {
        booksToShow = booksToShow.filter(book =>
          book.volumeInfo.title.toLowerCase().includes(title.toLowerCase())
        );
      }
      if (minPageRange) {
        booksToShow = booksToShow.filter(
          book => book.volumeInfo.pageCount > minPageRange
        );
      }
      if (maxPageRange) {
        booksToShow = booksToShow.filter(
          book => book.volumeInfo.pageCount < maxPageRange
        );
      }
      return booksToShow;
    }
  }
};
</script>
