import storageService from "./storageService";

export const bookService = {
  query,
  getById,
  addReview,
  getBooks
  // addUser,
  // removeUser
};

let gBooks = storageService.loadFromStorage('books') || query();

async function query(filterBy = 'witcher') {
  let books
  books = await fetch(`https://www.googleapis.com/books/v1/volumes?q=${filterBy}&maxResults=40&key=AIzaSyAXsefBbuz_jTbmr1vlxg7VvDh5bYEg7nA`)
  books = await books.json()
  gBooks = books.items
  storageService.saveToStorage('books', gBooks)
  return gBooks

}
async function getBooks() {
  return gBooks
}
async function getById(bookId) {
  return await gBooks.find(book => book.id === bookId);
}
async function addReview(bookId, review) {
  review.id = _makeid()
  let bookReviewed;

  gBooks = gBooks.map(book => {
    if (book.id === bookId) {
      if (book.reviews) {
        book.reviews = [
          review,
          ...book.reviews
        ]
      } else {
        book.reviews = [
          review
        ]
      }
      bookReviewed = { ...book };
    }
    return book
  })
  storageService.saveToStorage('books', gBooks)
  return bookReviewed
}
function _makeid(length = 10) {
  var text = "";
  var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

  for (var i = 0; i < length; i++) {
    text += possible.charAt(Math.floor(Math.random() * possible.length));
  }

  return text;
}
